"use client"

import type React from "react"
import { createContext, useContext, useEffect, useState } from "react"
import { useRouter } from "next/navigation"

export interface User {
  id: string
  email: string
  name: string
  role: "influencer" | "brand" | "admin"
  profile?: {
    bio: string
    avatar: string
    followers: number
    engagementRate: number
    niche: string
  }
  wallet?: {
    balance: number
    totalEarned: number
    pending: number
  }
}

interface AuthContextType {
  user: User | null
  token: string | null
  loading: boolean
  login: (email: string, password: string) => Promise<void>
  register: (name: string, email: string, password: string, role: "influencer" | "brand") => Promise<void>
  logout: () => void
  setToken: (token: string | null) => void
  setUser: (user: User | null) => void
}

const AuthContext = createContext<AuthContextType | undefined>(undefined)

export function AuthProvider({ children }: { children: React.ReactNode }) {
  const [user, setUser] = useState<User | null>(null)
  const [token, setToken] = useState<string | null>(null)
  const [loading, setLoading] = useState(true)
  const router = useRouter()

  // Load token from localStorage on mount
  useEffect(() => {
    const savedToken = localStorage.getItem("auth_token")
    const savedUser = localStorage.getItem("auth_user")

    if (savedToken && savedUser && savedUser !== "undefined") {
      setToken(savedToken)
      try {
        setUser(JSON.parse(savedUser))
      } catch (error) {
        console.error("[v0] Failed to parse saved user:", error)
        // Clear invalid data
        localStorage.removeItem("auth_user")
      }
    }

    setLoading(false)
  }, [])

  const login = async (email: string, password: string) => {
    try {
      setLoading(true)
      const response = await fetch("/api/auth/login", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ email, password }),
      })

      if (!response.ok) {
        const error = await response.json()
        throw new Error(error.error || "Login failed")
      }

      const data = await response.json()

      localStorage.setItem("auth_token", data.token)
      localStorage.setItem("auth_user", JSON.stringify(data.user))

      setToken(data.token)
      setUser(data.user)

      if (data.user.role === "admin") {
        router.push("/admin")
      } else if (data.user.role === "brand") {
        router.push("/client")
      } else {
        router.push("/influencer")
      }
    } finally {
      setLoading(false)
    }
  }

  const register = async (name: string, email: string, password: string, role: "influencer" | "brand") => {
    try {
      setLoading(true)
      const response = await fetch("/api/auth/register", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ name, email, password, role }),
      })

      if (!response.ok) {
        const error = await response.json()
        throw new Error(error.error || "Registration failed")
      }

      const data = await response.json()

      localStorage.setItem("auth_token", data.token)
      localStorage.setItem("auth_user", JSON.stringify(data.user))

      setToken(data.token)
      setUser(data.user)

      if (role === "brand") {
        router.push("/client")
      } else {
        router.push("/influencer")
      }
    } finally {
      setLoading(false)
    }
  }

  const logout = () => {
    localStorage.removeItem("auth_token")
    localStorage.removeItem("auth_user")
    setToken(null)
    setUser(null)
    router.push("/")
  }

  const setTokenFn = (newToken: string | null) => {
    setToken(newToken)
    if (newToken) {
      localStorage.setItem("auth_token", newToken)
      // fetchUserFromToken(newToken) - This was causing conflicts on second login
    }
  }

  const setUserFn = (newUser: User | null) => {
    setUser(newUser)
    if (newUser) {
      localStorage.setItem("auth_user", JSON.stringify(newUser))
    }
  }

  const fetchUserFromToken = async (token: string) => {
    try {
      const response = await fetch("/api/user/profile", {
        headers: { Authorization: `Bearer ${token}` },
      })
      if (response.ok) {
        const userData = await response.json()
        setUser(userData)
        localStorage.setItem("auth_user", JSON.stringify(userData))
      }
    } catch (error) {
      console.error("[v0] Failed to fetch user from token:", error)
    }
  }

  return (
    <AuthContext.Provider
      value={{ user, token, loading, login, register, logout, setToken: setTokenFn, setUser: setUserFn }}
    >
      {children}
    </AuthContext.Provider>
  )
}

export function useAuth() {
  const context = useContext(AuthContext)
  if (!context) {
    throw new Error("useAuth must be used within AuthProvider")
  }
  return context
}
